let contextBookmark = null

const Sanitizer = {
  entity: /[&<>"']/g,

  entities: {
    '&': '&amp;',
    '<': '&lt;',
    '>': '&gt;',
    '"': '&quot;',
    '\'': '&apos;',
  },

  getEntity(value) {
    return Sanitizer.entities[value]
  },

  /**
   * Escapes HTML for all values in a tagged template string.
   */
  escapeString(str) {
    let result = ''

    for (let i = 0; i < str.length; i++) {
      result += str[i].replace(Sanitizer.entity, Sanitizer.getEntity)
    }

    return result
  }
}

const RequestManager = {
  controllers: {},

  createRequestController(key) {
    // Initialize AbortController to cancel excess requests
    const newController = new AbortController()

    RequestManager.controllers[key] = newController

    return newController
  },

  cancelRequest(key) {
    const controller = RequestManager.controllers[key]

    if (!controller) return

    controller.abort()
  },
}

// Handle install/updated/uninstall
chrome.runtime.onInstalled.addListener((details) => {
  switch (details.reason) {
    case 'install': return afterInstall()
    case 'update': return afterUpdate()
  }
})

/**
 * Opens the landing page for the installed extension
 */
function afterInstall() {
  // Perform welcome ceremony
  if (WINDOW_CURRENT_BROWSER !== 'torch') welcome()
}

/**
 * Makes sure all required optional privileges are there after updated
 */
function afterUpdate() {
  if (WINDOW_CURRENT_BROWSER !== 'torch') welcome()
}

/**
 * Performs the welcome ceremony
 */
function welcome() {
  showWelcomePage(false /* Always show welcome page in a window */).then(({ openSignIn }) => {
    if (openSignIn) {
      // Open sign-in page
      goToSignIn()
    } else {
      // On updates, just go to the homepage
      showTour()
    }
  }).catch((err) => { })
}

/**
 * Opens the homepage
 */
function openHomepage() {
  chrome.tabs.create({ url: Startme.apiURL('/users/home') })
}

/**
 * Opens bookmarks manager
 */
function openBookmarksManager() {
  chrome.tabs.create({ url: Startme.apiURL('/bookmarks?section=inbox') })
}

/**
 * Opens the welcome page
 */
function showWelcomePage(fullScreen = true) {
  return new Promise((resolve, reject) => {
    // showPopup(chrome.runtime.getURL('welcome.html'), fullScreen ? 'fullscreen' : undefined)
    chrome.tabs.create({ url: chrome.runtime.getURL('welcome.html') })
      .then(tab => waitForTabToLoad(tab))
      .then((tab) => {
        return chrome.tabs.sendMessage(tab.id, { message: SM_MESSAGE_SHOW_WELCOME })
        // return Promise.resolve()
      })
      .then((data) => {
        if (chrome.runtime.lastError) {
          return reject()
        }

        resolve(data)
      })
  });
}

function waitForTabToLoad(tab) {
  return new Promise((resolve, reject) => {
    // See if tab has been loaded already
    if (tab.status == chrome.tabs.TabStatus.COMPLETE) return resolve()

    // Create listener for this tab specifically
    let listener = (tabId, changes, tab) => {
      if (changes.status != chrome.tabs.TabStatus.COMPLETE) return // Wait a little more

      // Uninstall listener and resolve promise
      chrome.tabs.onUpdated.removeListener(listener)
      resolve(tab)
    }

    // Install listener
    chrome.tabs.onUpdated.addListener(listener)
  })
}

/**
 * Logouts the user and opens sign-in page
 */
function goToSignIn() {
  Startme.clearCache()
  return newTab(Startme.apiURL('/users/sign_out'))
}

/**
 * Shows a sign in dialog. Reloads the specified tab after sign in
 */
function signIn() {
  Startme.clearCache()
  return newTab(Startme.apiURL('/users/sign_in'))
}

/**
 * Shows the tour
 */
function showTour() {
  return newTab(Startme.apiURL('/users/new_extension_installed'))
}

