// Adds context menu entries
function addContextMenuOptions() {
  chrome.contextMenus.create({
    id: SM_COMMAND_OPEN_HOMEPAGE,
    title: chrome.i18n.getMessage('open_startme_homepage'),
    type: 'normal',
    contexts: ['action'],
  }, () => chrome.runtime.lastError)

  chrome.contextMenus.create({
    id: SM_CONTEXT_MENU_IMPORT,
    title: chrome.i18n.getMessage('import_bookmarks_from_chrome'),
    type: 'normal',
    contexts: ['action'],
  }, () => chrome.runtime.lastError)

  chrome.contextMenus.create({
    id: SM_CONTEXT_MENU_HELP,
    title: chrome.i18n.getMessage('help'),
    type: 'normal',
    contexts: ['action'],
  }, () => chrome.runtime.lastError)

  chrome.contextMenus.create({
    id: SM_CONTEXT_MENU_BOOKMARK_LINK,
    title: chrome.i18n.getMessage('add_to_startme'),
    type: 'normal',
    contexts: ['link'],
    targetUrlPatterns: ['http://*/*', 'https://*/*']
  }, () => chrome.runtime.lastError)

  if (WINDOW_CURRENT_BROWSER === 'firefox') {
    chrome.contextMenus.create({
      id: SM_CONTEXT_MENU_OPTIONS,
      title: chrome.i18n.getMessage('preferences'),
      type: 'normal',
      contexts: ['action'],
    }, () => chrome.runtime.lastError)
  }
}

function addContextListeners(info, tab) {
  switch (info.menuItemId) {
    case SM_COMMAND_OPEN_HOMEPAGE:
      return openHomepage()
    case SM_CONTEXT_MENU_IMPORT:
      return importBookmarks()
    case SM_CONTEXT_MENU_BOOKMARK_LINK:
      return showBookmarkBarInPopup(info.linkUrl, info.selectionText || null, tab && tab.id)
    case SM_CONTEXT_MENU_HELP:
      return showHelp()
    case SM_CONTEXT_MENU_OPTIONS:
      return showOptions()
  }
}

try {
  addContextMenuOptions()
} catch (e) { }

chrome.contextMenus.onClicked.addListener(addContextListeners)

chrome.runtime.onMessage.addListener(function (request, sender, respond) {
  if (request && request.message) {
    switch (request.message) {
      case SM_MESSAGE_OPEN_URL:
        getHomepageUrl().then(function (url) {
          respond({ url })
        })
        return true
      case SM_MESSAGE_SIGN_IN:
        signIn()
        return false
      case SM_MESSAGE_SHOW_HELP_PAGE:
        showHelpPage()
        return false
      case SM_MESSAGE_SIGN_OUT:
        goToSignIn()
        return false
      case SM_MESSAGE_OPEN_BOOKMARKS:
        openBookmarksManager()
        return false
      case SM_MESSAGE_GET_PAGES:
        getPages(!!request.noCache)
          .then((pages) => {
            respond(pages)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_GET_USER_PAGES:
        getUserPages(!!request.noCache)
          .then((pages) => {
            respond(pages)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_IMPORT:
        importBookmarks(!!request.quiet).then(url => respond(url));
        return true; // Allow for asynchronous response
      case SM_MESSAGE_CHECK_LINKS_AUTOSAVING:
        checkLinksAutosaving()
          .then((res) => {
            respond(res)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_AUTOSAVE_LINK:
        autosaveLink({ bookmarkData: request.bookmarkData })
          .then((res) => {
            respond(res)
          })
          .catch((error) => {
            respond(false)
          })
        return true // Allow for asynchronous response
      case SM_MESSAGE_AUTOSAVE_LINKS:
        autosaveLinks({ bookmarksData: request.bookmarksData })
          .then((res) => {
            respond(res)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_EDIT_AUTOSAVED_LINK:
        editAutosavedLink({ id: request.id, bookmarkData: request.bookmarkData })
          .then((res) => {
            respond(res)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_CHANGE_AUTOSAVING:
        changeLinksAutosaving(request.requestData)
          .then((res) => {
            respond(res)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_DELETE_AUTOSAVED_LINK:
        removeAutosavedLink({ id: request.id })
          .then((res) => {
            respond(res)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_ADD_BOOKMARKS_BATCH:
        addBookmarksBatch({ widgetId: request.widgetId, bookmarks: request.bookmarks })
          .then((res) => {
            respond(res)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_ADD_NEW_BOOKMARK:
        addNewBookmark({ widgetId: request.widgetId, bookmark: request.bookmark })
          .then((res) => {
            respond(res)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_CHECK_LINK_EXISTS:
        checkLinkExists({ widgetId: request.widgetId, groupId: request.groupId, url: request.url })
          .then((res) => {
            respond(res)
          })
          .catch(error => respond(false))
        return true // Allow for asynchronous response
      case SM_MESSAGE_ADD_NEW_WIDGET:
        addWidget({ widget: request.widget, pageId: request.pageId })
          .then((res) => {
            respond(res)
          })
          .catch(error => {
            respond(false)
          })
        return true // Allow for asynchronous response
      case SM_MESSAGE_CONTEXT_LINK:
        respond(contextBookmark ? { ...contextBookmark } : null)
        contextBookmark = null
        return true // Allow for asynchronous response
      case SM_MESSAGE_CLEAR_CACHE:
        Startme.clearCache()
        return false
    }
  }
})

/**
 * Listen omnibox on Enter hit
 */
chrome.omnibox.onInputEntered.addListener((text) => {
  chrome.tabs.query({ active: true, currentWindow: true })
    .then((tabs) => {
      if (tabs.length != 1) return
      let url

      if (SM_HTTP_URL_PATTERN.test(text)) url = text
      else url = STARTME_SEARCH_PAGE_PATTERN.replace('{query}', encodeURIComponent(text))

      chrome.tabs.update(tabs[0].id, { url })
    })
})

/**
 * Listen omnibox on change
 */
 chrome.omnibox.onInputChanged.addListener((text, suggest) => {
  let userData

  chrome.storage.local.get(['cachedUser'], (result) => {
    try {
      userData = JSON.parse(result.cachedUser)
    } catch (e) { }

    if (!userData || !userData.user) {
      Startme.getUser(true /* Force check */)
        .then(() => {
          initSearch(text, suggest)
        })
        .catch(() => { })
    } else {
      initSearch(text, suggest)
    }
  })
})

function initSearch(text, suggest) {
  searchForBookmarks(text)
    .then((bookmarks) => {
      suggest(bookmarks)
    })
    .catch((err) => {
      suggest([])
    })
}

function searchForBookmarks(query = '') {
  return new Promise((resolve, reject) => {
    if (query.trim().length < 2) {
      resolve([])
      return
    }

    chrome.storage.local.get(['cachedUser'], (result) => {
      let userData

      try {
        userData = JSON.parse(result.cachedUser)
      } catch (e) { }

      if (!userData || !userData.user) {
        resolve([])
        return
      }

      RequestManager.cancelRequest('omnibox')
      const controller = RequestManager.createRequestController('omnibox')

      fetch(Startme.apiURL(`/u/${userData.user.public_id}/extension_bookmarks.json?query=${query.trim()}`), {
        method: 'GET',
        credentials: 'include',
        signal: controller.signal,
      })
        .then(response => {
          return response.json()
        })
        .then((data = {}) => {
          if (!data.bookmarks || !data.bookmarks.length) {
            resolve([])
            return
          }

          const suggestions = formSuggestions(data.bookmarks, query.trim())
          resolve(suggestions)
        }).catch(() => {
          resolve([])
        })
        .catch((err) => {
          if (controller.signal.aborted) return

          resolve([])
        })
    })
  })
}

function formSuggestions(bookmarks, query) {
  const formedBookmarks = bookmarks
    .sort((a, b) => (a.title || '').toLowerCase() > (b.title || '').toLowerCase() ? 1 : -1)
    .map((bookmark) => {
      const description = `${markMatches(Sanitizer.escapeString(bookmark.title), query)}${wrapString(Sanitizer.escapeString(bookmark.url))}`

      return {
        content: bookmark.url,
        description,
      }
    })

  return formedBookmarks
}

function markMatches(text, query = '') {
  if (WINDOW_CURRENT_BROWSER !== 'firefox') return text

  const re = new RegExp(`(.*?)(${query.trim()})(.*)`, 'i')
  const results = re.exec(text)

  if (!results) return `<match>${text}</match>`

  let html = ''

  if (results[1]) html += results[1]
  html += `<match>${results[2]}</match>`
  if (results[3]) html += results[3]

  return html
}

function wrapString(text) {
  if (WINDOW_CURRENT_BROWSER !== 'firefox') return ` - ${text}`
  return wrapStringDim(text)
}

function wrapStringDim(text) {
  return `<dim> - ${text}</dim>`
}

// Listen for external messages
chrome.runtime.onMessageExternal.addListener((request, sender, respond) => {
  switch (request.message) {
    case SM_MESSAGE_OPEN_URL:
      // Let extension open a specific URL (not openable from a plain HTML-page)
      if (request.openInNewWindow) {
        chrome.tabs.create({ url: request.url, active: !!request.active })
      } else {
        chrome.tabs.update({ url: request.url })
      }
      respond(true) // Send some feedback
      return false

    case SM_MESSAGE_CLEAR_CACHE:
      Startme.clearCache()
      return false

    case SM_MESSAGE_HOMEPAGE_FETCHED:
      saveHomepageBackground(request.page)
      return false

    case SM_MESSAGE_RESET_HOMEPAGE_ID:
      resetHomepageId({ pageId: request.pageId, pagePublicId: request.pagePublicId })
      return false
  }
})

/**
 * Resets current homepage if the same page id was passed
 * @returns {Promise}
 */
 function resetHomepageId ({ pageId, pagePublicId }) {
  chrome.storage.local.get([SM_OPTION_HOMEPAGE_ID, SM_OPTION_HOMEPAGE_URL], (result) => {
    const homepageId = result[SM_OPTION_HOMEPAGE_ID]
    const homepageUrl = result[SM_OPTION_HOMEPAGE_URL]

    if (!homepageId) {
      if (homepageUrl) chrome.storage.local.remove([SM_OPTION_HOMEPAGE_URL])

      changeIframeSrc({ homepageUrl, url: Startme.apiURL('/users/home') })
      return
    }

    let sourcePageId = pageId

    if (sourcePageId && sourcePageId === homepageId) {
      clearHomepageStorageData()
    } else if (!sourcePageId && pagePublicId) {
      const url = Startme.apiURL(`/tools/get_page_id/${pagePublicId}`)

      fetch(url, { credentials: 'include' })
        .then(response => {
          if (response.status != 200) return Promise.resolve()

          return response.json()
        })
        .then((data) => {
          if (data.page_id) sourcePageId = data.page_id
          if (sourcePageId && sourcePageId === homepageId) clearHomepageStorageData()
        })
    }
  })
}

function clearHomepageStorageData() {
  chrome.storage.local.get([SM_OPTION_HOMEPAGE_URL], (result) => {
    const homepageUrl = result[SM_OPTION_HOMEPAGE_URL]
    changeIframeSrc({ homepageUrl, url: Startme.apiURL('/users/home') })

    chrome.storage.local.remove([SM_OPTION_HOMEPAGE_ID, SM_OPTION_HOMEPAGE_URL])
  })
}

function changeIframeSrc(options = {}) {
  const { homepageUrl, url } = options
  if (!homepageUrl || !url) return

  chrome.tabs.query({ active: true, currentWindow: true })
    .then((tabs) => {
      if (tabs.length != 1) return
      const tabId = tabs[0].id

      chrome.tabs.sendMessage(tabId, {
        message: SM_MESSAGE_CHECK_IFRAME_SRC,
        homepageUrl,
        url,
      })
    })
}

function getHomepageUrl() {
  return new Promise(function (resolve) {
    chrome.storage.local.get([SM_OPTION_HOMEPAGE_URL], (result) => {
      const homepageUrl = result[SM_OPTION_HOMEPAGE_URL]

      if (homepageUrl && homepageUrl.startsWith('/p/')) {
        resolve(Startme.apiURL(homepageUrl))
      } else if (homepageUrl) {
        resolve(homepageUrl)
      } else {
        resolve(Startme.apiURL('/users/home'))

        getHomepage()
          .then((homepage) => {
            chrome.storage.local.set({
              [SM_OPTION_HOMEPAGE_ID]: homepage.id,
              [SM_OPTION_HOMEPAGE_URL]: homepage.url,
            }, () => {})
          })
          .catch((err) => { })
      }
    })
  })
}

function getHomepage() {
  return new Promise(function (resolve, reject) {
    chrome.storage.local.get([SM_OPTION_HOMEPAGE_ID], (result) => {
      const homepageId = result[SM_OPTION_HOMEPAGE_ID]

      getPages()
        .then((pages) => {
          let homepage = pages.find(page => page.id === parseInt(homepageId))

          if (!homepage) homepage = pages.shift()
          if (homepage) resolve(homepage)
          else reject()
        })
        .catch(reject)
    })
  })
}

