/**
 * Initiates bookmark import
 */
function importBookmarks(quiet = false) {
  return new Promise((resolve, reject) => {
    if (quiet) {
      importBookmarkTree()
        .then(importId => resolve(importId))
        .catch(reject)
    } else {
      let importTabId;
      showPopup(chrome.runtime.getURL('import.html'), 1000, 600)
        .then(tab => {
          importTabId = tab.id;

          return importBookmarkTree()
        })
        .then(importId => {
          startPolling(importId)
            .then(importedPageUrl => {
              // Close the import tab
              chrome.tabs.remove(importTabId)

              // Open the imported page
              if (importedPageUrl) {
                chrome.tabs.create({
                  url: importedPageUrl
                })
                resolve(importedPageUrl)
              } else {
                reject()
              }
            })
        })
        .catch(reject)
    }
  })
}

/**
 * Imports all bookmarks
 * @returns {Promise}
 */
function importBookmarkTree() {
  return new Promise((resolve, reject) => {
    chrome.bookmarks.getTree()
      .then((nodes) => {
        var data = new FormData()
        data.append('page_id', '')
        data.append('title', 'Imported Bookmarks')
        data.append('import_content', buildImportFile(nodes))

        fetch(Startme.apiURL('/import'), {
          method: 'POST',
          credentials: 'include',
          body: data
        })
          .then(response => {
            if (response.status != 200) return reject()

            response.json().then(data => {
              if (data.id > 0) resolve(data.id); else reject()
            }).catch(reject)
          })
      })
  })

  function buildImportFile(nodes) {
    let content =
      "<!DOCTYPE NETSCAPE-Bookmark-file-1>\n" +
      "<!-- This is an automatically generated file.\n" +
      "     It will be read and overwritten.\n" +
      "     DO NOT EDIT! -->\n" +
      "<META HTTP-EQUIV=\"Content-Type\" CONTENT=\"text/html; charset=UTF-8\">\n" +
      "<TITLE>Imported Bookmarks</TITLE>\n" +
      "<H1>Imported Bookmarks</H1>\n" +
      "<DL><p>\n"

    for (let i = 0; i < nodes.length; i++) {
      content += generateBookmark(nodes[i])
    }

    content += "</p></DL>\n"

    return content
  }

  /**
   * Converts the specified node into an HTML fragment
   * @param node
   * @returns {string}
   */
  function generateBookmark(node) {
    var content = ""

    if (node.children && node.children.length > 0) {
      content +=
        "<DT><H3 ADD_DATE=\"" + node.dateAdded + "\" LAST_MODIFIED=\"" + node.dateGroupModified + "\">" + node.title + "</H3></DT>\n" +
        "<DL><p>\n"

      for (var i = 0; i < node.children.length; i++) {
        content += generateBookmark(node.children[i])
      }

      content += "</p></DL>\n"
    }

    if (node.url) {
      content += "<DT><A HREF=\"" + node.url + "\" ADD_DATE=\"" + node.dateAdded + "\">" + node.title + "</A></DT>\n"
    }

    return content
  }
}

/**
 * Starts polling for import status
 * @param importId
 */
function startPolling(importId) {
  let attempts = 0, interval = 1000

  return new Promise((resolve, reject) => {
    // Start first attempt asap
    setTimeout(attempt, 1)

    function attempt() {
      poll(importId).then(url => {
        attempts++;
        if (url) {
          // Succeeded!
          resolve(url + '?import=' + importId)
        } else {
          // Try again later
          setTimeout(attempt, interval += 1000)
        }
      }).catch(error => {
        // Switch to error state
        document.documentElement.classList.add('error')
        reject()
      })
    }
  })
}

/**
 * Polls the status of the specified import
 * @param importId
 * @returns {Promise}
 */
function poll(importId) {
  return new Promise((resolve, reject) => {
    fetch(Startme.apiURL('/import/poll/' + importId), {
      credentials: 'include'
    }).then(response => {
      // See if we got a valid response
      if (response.status != 200 && response.status != 201) return reject()

      // Analyze response
      response.json().then(data => {
        if (response.status == 201) {
          return resolve(data.location)
        } else {
          switch (data.status) {
            case 'work':
              // Still working
              return resolve()
            case 'error':
              return reject(data.message)
            default:
              reject()
          }
        }
      })
    }).catch(reject)
  })
}

/**
 * Opens a new tab
 * @param url
 * @returns {Promise}
 */
function newTab(url) {
  return new Promise((resolve, reject) => {
    chrome.tabs.create({ url })
    .then((tab) => {
      if (chrome.runtime.lastError) return reject(chrome.runtime.lastError)
      resolve(tab)
    })
  })
}

/**
 * Opens a popup window with the specified dimensions
 * @param url
 * @param width
 * @param height
 * @returns {Promise}
 */
function showPopup(url, width = 0.8, height = 0.8, centerX = 0.5, centerY = 0.5) {
  return new Promise((resolve, reject) => {
    // Compose create options
    let options = {
      url,
      type: chrome.windows.WindowType.POPUP
    }

    if (WINDOW_CURRENT_BROWSER !== 'firefox') options.focused = true

    let screenWidth, screenHeight

    // Determine position for popup window
    switch (width) {
      case 'fullscreen':
        options.state = chrome.windows.WindowState.FULLSCREEN
        break
      default:
        chrome.windows.getCurrent()
          .then((currentWindow) => {
            if (currentWindow) {
              screenWidth = currentWindow.width
              screenHeight = currentWindow.height

              width = parseFloat(width) || 0.8
              height = parseFloat(height) || 0.8

              if (width <= 1) width = screenWidth * width
              if (height <= 1) height = screenHeight * height

              options.width = Math.round(Math.min(screenWidth, width))
              options.height = Math.round(Math.min(screenHeight, height))
              options.left = Math.round((screenWidth - width) * centerX)
              options.top = Math.round((screenHeight - height) * centerY)

              createWindow(options)
            } else {
              options.state = chrome.windows.WindowState.FULLSCREEN

              createWindow(options)
            }
          })
          .catch(() => {
            options.state = chrome.windows.WindowState.FULLSCREEN

            createWindow(options)
          })
    }

    // Create window
    function createWindow (options) {
      chrome.windows.create(options)
        .then((window) => {
          return chrome.tabs.query({ windowId: window.id, index: 0 })
        })
        .then((tabs) => {
          if (tabs.length > 0) {
            resolve(tabs[0])
          } else {
            reject()
          }
        })
        .catch(() => {})
    }
  })
}

/**
 * Listens for extension commands
 */
chrome.commands.onCommand.addListener((command, tab) => {
  switch (command) {
    case SM_COMMAND_OPEN_HOMEPAGE:
      openHomepage()
      break
    case SM_COMMAND_SAVE_BOOKMARK:
      showBookmarkBarInPopup(tab.url, tab.title || null, tab && tab.id)
      break
  }
})

/**
 * Save background of a homepage tab
 */
 function saveHomepageBackground(page = {}) {
  chrome.storage.local.get([SM_OPTION_HOMEPAGE_ID], (result) => {
    const homepageId = result[SM_OPTION_HOMEPAGE_ID]

    if (!homepageId || page.id !== homepageId) return

    const dataToSave = {}
    if (page.background !== null) {
      dataToSave.backgroundImage = getImageUrl(page.background)
      dataToSave.backgroundColor = null
    } else if (page.theme_color !== null) {
      dataToSave.backgroundColor = getHexColor(page.theme_color)
      dataToSave.backgroundImage = null
    }

    dataToSave.isDarkTheme = !!page.isDarkTheme

    chrome.storage.local.set(dataToSave, () => {})
  })
}

function getHexColor(color) {
  if (color === null) return null

  const r = (color >> 16) & 0xff
  const g = (color >> 8) & 0xff
  const b = color & 0xff

  return `#${((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1)}`
}

function getImageUrl(imageId) {
  if (!imageId) return null

  const defaultParams = { f: 'auto', q: 'auto' }

  // Format image parameters string
  const cloudinaryProps = Object.entries(defaultParams).map(([key, value]) => `${key}_${value}`)

  const image = `${cloudinaryProps.join(',')}/${imageId}`

  return `${CLOUDINARY_HOST}/${image}`
}

chrome.tabs.onCreated.addListener(() => {
  if (!Startme.checkExtensionInfoTimestampExpired()) return

  const url = Startme.apiURL(`/tools/register_extension_presence?extension_type=${EXTENSION_TYPE}&browser=${WINDOW_CURRENT_BROWSER}&version=${CURRENT_VERSION}`)
  Startme.updateExtensionInfoTimestamp()

  fetch(url, { credentials: 'include' }).catch((err) => { })
})
